import { useState } from "react";
import "./App.css";
import AppHeader from "./components/AppHeader";
import AppSearch from "./components/AppSearch";
import ImageItem from "./components/ImageItem";
import ImagePost from "./components/ImagePost";
import imageData from "./data/imageData";

function App() {
  const [selectedImage, setSelectedImage] = useState(null);
  const [searchText, setSearchText] = useState("");

  function onImageOpenClick(theImage) {
    setSelectedImage(theImage);
  }

  function onImageCloseClick() {
    setSelectedImage(null);
  }

  const filteredImages = imageData.filter((image) => {
    return image.title.includes(searchText);
  });

  const imageElement = imageData
    .filter((image) => {
      return image.title.includes(searchText);
    })
    .map((image, index) => {
      return (
        <ImageItem
          key={index}
          myimage={image}
          onImageClick={onImageOpenClick}
        />
      );
    });

  let imagePost = null;
  if (!!selectedImage) {
    imagePost = (
      <ImagePost image={selectedImage} onBgClick={onImageCloseClick} />
    );
  }

  return (
    <div className="App">
      <AppHeader />
      <section className="app-section">
        <section className="app-content">
          <AppSearch value={searchText} onValueChange={setSearchText} />
          <div class="app-grid">{imageElement}</div>
        </section>
      </section>
      {imagePost}
    </div>
  );
}

export default App;
